import "./App.css";
import NavBar from "./components/NavBar";
import { DateTime } from "luxon";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { CssBaseline } from "@mui/material";
import {
  MapContainer,
  TileLayer,
  Marker,
  Tooltip,
  Polyline,
} from "react-leaflet";
import L from "leaflet";
import useSWR from "swr";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

const boatIcon = new L.Icon({
  iconUrl: "/img/circle.svg",
  iconRetinaUrl: "/img/circle.svg",
  iconAnchor: null,
  popupAnchor: null,
  shadowUrl: null,
  shadowSize: null,
  shadowAnchor: null,
  iconSize: new L.Point(20, 50),
});

const fetcher = (url) => fetch(url).then((res) => res.json());

function generatePolyline(ship) {
  var output = [];
  output.push([ship.latitude, ship.longitude]);
  for (let i = 0; i < ship.track.length; i++) {
    output.push([ship.track[i].latitude, ship.track[i].longitude]);
  }
  return output;
}

function App() {
  const { data, error } = useSWR(
    "https://tesla-ship-locations.s3.eu-west-2.amazonaws.com/locations.json",
    fetcher
  );
  if (error) return <h1>error, please come back later</h1>;
  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <div>
        {data && (
          <>
            <NavBar />
            <MapContainer
              center={[0, 60]}
              zoom={3}
              zoomControl={false}
              style={{ width: "100vw", height: "95vh" }}
            >
              <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
                url="https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png"
              />
              <TileLayer
                attribution='Map data: &copy; <a href="http://www.openseamap.org">OpenSeaMap</a> contributors <a href="https://www.privacypolicygenerator.info/live.php?token=J3RMtrNoYamqzga742f8Pru3uiWcW7ih">Privacy Policy</a>'
                url="https://tiles.openseamap.org/seamark/{z}/{x}/{y}.png"
              />
              {data.locations.map((ship, i) => {
                return (
                  <>
                    <Marker
                      position={[ship.latitude, ship.longitude]}
                      icon={boatIcon}
                    >
                      <Tooltip direction="bottom" permanent offset={[0, 10]}>
                        <span>
                          <b>{ship.name}</b>
                        </span>
                        <br />
                        <span>to <b>{ship.destination}</b></span>
                        <br />
                        <span><i>arriving <b>{DateTime.fromISO(ship.eta).toRelative()}</b></i></span>
                        <br />
                        <span><b>{ship.speed}</b> kts</span>
                        <br />
                        <br />
                        <span>
                          Recieved Via <b>{ship.source}</b>
                        </span>
                        <br />
                        <span>
                          <i>{DateTime.fromISO(ship.timestamp).toRelative()}</i>
                        </span>
                      </Tooltip>
                    </Marker>
                    <Polyline positions={generatePolyline(ship)} />
                  </>
                );
              })}
            </MapContainer>
          </>
        )}
      </div>
    </ThemeProvider>
  );
}

export default App;
